<template>
    <v-form ref="form" v-model="valid" lazy-validation>
        <v-text-field
            v-model="name"
            :counter="30"
            :rules="[
                (v) => !!v || $t('contact.name_required'),
                (v) => (v && v.length <= 30) || $t('contact.name_less30'),
            ]"
            label="Name"
            required
        ></v-text-field>
        <v-select
            v-model="select"
            :items="[
                $t('contact.private_message'),
                $t('contact.get_offer'),
                $t('contact.suggestion_for_improvement'),
            ]"
            :rules="[(v) => !!v || $t('contact.subject_required')]"
            :label="$t('contact.subject')"
            required
        ></v-select>

        <v-textarea
            outlined
            v-model="message"
            :rules="[
                (v) => !!v || $t('contact.message_required'),
                (v) => (v && v.length >= 5) || $t('contact.message_more5'),
            ]"
            name="input-7-4"
            :label="$t('contact.message')"
            value=""
        >
        </v-textarea>

        <v-row>
            <v-spacer></v-spacer>
            <v-btn
                :disabled="!isValidated"
                color="secondary"
                class="mr-4"
                @click="validate"
                text
            >
                <v-icon xlarge>mdi-email</v-icon>
                <h2 class="font-italic">{{ $t("contact.send") }}...</h2>
            </v-btn>
        </v-row>
    </v-form>
</template>

<script>
const sendEmail = (receiver, name, subject, message) => {
    window.open(
        "mailto:".concat(
            receiver,
            "?subject=", subject,
            "&body=", message,
            "Regards ",name
        )
    );
};

export default {
    data: () => ({
        receiver: "raphael.mueller66@gmail.com",
        valid: false,
        name: "",
        message: "",
        select: null,
       }),

    methods: {
        validate() {
            if (this.$refs.form.validate()) {
                sendEmail(this.receiver, this.name, this.select, this.message);
            }
        },
        reset() {
            this.$refs.form.reset();
        },

        mounted() {
          this.valid = this.$refs.form.validate()
        },

        
    },
     computed: {
          isValidated() {
            console.log("sync" +this.valid)
            return this.valid 
          }
          
        } 
};
</script>
<style lang=""></style>
