<template>
    <v-snackbar
        bottom
        v-model="snackbar"
        dark
        :color="color"
        :timeout="timeout"
    >
        <v-container grid-list-xs>
            <v-row no-gutters>
                <v-col cols="11">
                    <span>{{ message }}</span>
                    </v-col>
                <v-col cols="1">
                    <v-icon  @click="snackbar = false">mdi-close</v-icon>
                </v-col>
            </v-row>
        </v-container>
    </v-snackbar>
</template>

<script>
import EventBus from "@/eventbus";

export default {
    name: "notificator",

    data() {
        return {
            message: "",
            color: "",
            snackbar: false,
            timeout: -1,
        };
    },

    created: function() {
        this.infoMesageHandler = (message) => {
            this.$data.color = "info";
            this.$data.message = message;
            this.$data.snackbar = true;
            this.$data.timeout = 5000;
        };

        this.successMessageHandler = (message) => {
            this.$data.color = "success";
            this.$data.message = message;
            this.$data.snackbar = true;
            this.$data.timeout = 5000;
        };

        this.errorMessageHandler = (message) => {
            this.$data.color = "error";
            this.$data.message = message;
            this.$data.snackbar = true;
            this.$data.timeout = 0;
        };

        EventBus.onInfoMessage(this.infoMesageHandler);
        EventBus.onSuccessMessage(this.successMessageHandler);
        EventBus.onErrorMessage(this.errorMessageHandler);
    },

    beforeDestroy: function() {
        EventBus.offInfoMessage(this.infoMesageHandler);
        EventBus.offSuccessMessage(this.successMessageHandler);
        EventBus.offErrorMessage(this.errorMessageHandler);
    },
};
</script>

