import Vue from 'vue';

const EventBus = new Vue();

const INFO_CHANNEL_NAME = 'infoMessages';
const SUCCESS_CHANNEL_NAME = 'successMessages';
const ERROR_CHANNEL_NAME = 'errorMessage';

const pushEvent = (EVENT_NAME, data) => {
    EventBus.$emit(EVENT_NAME, data);
};

const registerEvent = (EVENT_NAME, callback) => {
    EventBus.$on(EVENT_NAME, callback);
};

const showInfoMessage = (message) => {
    EventBus.$emit(INFO_CHANNEL_NAME, message);
};

const showSuccessMessage = (message) => {
    EventBus.$emit(SUCCESS_CHANNEL_NAME, message);
};

const showErrorMessage = (message) => {
    EventBus.$emit(ERROR_CHANNEL_NAME, message);
};

const onInfoMessage = (callback) => {
    EventBus.$on(INFO_CHANNEL_NAME, callback)
};

const onSuccessMessage = (callback) => {
    EventBus.$on(SUCCESS_CHANNEL_NAME, callback)
};

const onErrorMessage = (callback) => {
    EventBus.$on(ERROR_CHANNEL_NAME, callback)
};

const offInfoMessage = (callback) => {
    EventBus.$off(INFO_CHANNEL_NAME, callback)
};

const offSuccessMessage = (callback) => {
    EventBus.$off(SUCCESS_CHANNEL_NAME, callback)
};

const offErrorMessage = (callback) => {
    EventBus.$off(ERROR_CHANNEL_NAME, callback)
};

const Notification = {
    showInfoMessage : showInfoMessage,
    onInfoMessage : onInfoMessage,
    offInfoMessage : offInfoMessage,
    showSuccessMessage : showSuccessMessage,
    onSuccessMessage : onSuccessMessage,
    offSuccessMessage : offSuccessMessage,
    showErrorMessage : showErrorMessage,
    onErrorMessage : onErrorMessage,
    offErrorMessage : offErrorMessage,
    pushEvent: pushEvent,
    registerEvent: registerEvent
};

export default Notification
