import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Work from '../views/Work.vue'
import Hobby from '../views/Hobby.vue'
import Values from '../views/Values.vue'
import Travel from '../views/Travel.vue'
import Offer from '../views/Offer.vue'
import Contact from '../views/Contact.vue'
import Guestbook from '../views/Guestbook.vue'
import About from '../views/About.vue'
import Login from '../views/Login.vue'
import Signup from '../views/Signup.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/work',
    name: 'Work',
    component: Work
  },
  {
    path: '/hobby',
    name: 'Hobby',
    component: Hobby
  },
  {
    path: '/values',
    name: 'Values',
    component: Values
  },
  {
    path: '/travel',
    name: 'Travel',
    component: Travel
  },
  {
    path: '/offer',
    name: 'Offer',
    component: Offer
  },
  {
    path: '/contact',
    name: 'Contact',
    component: Contact
  },
  {
    path: '/guestbook',
    name: 'Guestbook',
    component: Guestbook
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/signup',
    name: 'Signup',
    component: Signup
  }
]

const router = new VueRouter({
  routes
})

export default router
