<template>
    <v-container grid-list-xs>
        <v-row dense align="center" justify="center">
            <v-col align="center" justify="center" cols="12" md="8" sm="8">
                <v-form
                    ref="form"
                    class="loginform"
                    v-model="valid"
                    lazy-validation
                >
                    <v-text-field
                        v-model="nickname"
                        class="text-h6 text-sm-h6 text-md-h6 text-lg-h5 text-xl-h5 font-weight-light"
                        :rules="[
                            (v) => !!v || $t('required'),
                            (v) =>
                                v.length >= 6 ||
                                $t('login_page.nickname lengt'),
                        ]"
                        counter
                        :label="$t('nickname')"
                        required
                    >
                    </v-text-field>

                    <v-text-field
                        v-model="email"
                        class="text-h6 text-sm-h6 text-md-h6 text-lg-h5 text-xl-h5 font-weight-light"
                        :rules="[
                            (v) => !!v || $t('login_page.email required'),
                            (v) =>
                                /.+@.+\..+/.test(v) ||
                                $t('login_page.email validity'),
                        ]"
                        label="E-mail"
                        required
                    ></v-text-field>
                    <v-row>
                        <v-spacer></v-spacer>

                        <v-btn
                            :disabled="!isValidated"
                            color="secondary"
                            class="text-subtitle-2 text-sm-subtitle-2 text-md-h6 text-lg-h5 text-xl-h5 font-italic font-weight-light ma-2 mt-3"
                            @click="logInOut"
                            tile
                        >
                            <v-icon left v-if="isLogin">mdi-login</v-icon
                            ><v-icon left v-else>mdi-logout</v-icon>
                            {{ $t(`navigation.${label}`) }}
                        </v-btn>
                        <v-btn
                            color="secondary"
                            class="text-subtitle-2 text-sm-subtitle-2 text-md-h6 text-lg-h5 text-xl-h5 font-italic font-weight-light  mt-3"
                            @click="toSignup"
                            tile
                            ><v-icon left>mdi-account-plus</v-icon>
                            {{ $t("navigation.sign up") }}
                        </v-btn>
                    </v-row>
                    <v-row align-end justify-end dense>
                        <v-spacer></v-spacer>
                        <v-btn
                            color="secondary"
                            class="text-subtitle-2 text-sm-subtitle-2 text-md-h6 text-lg-h5 text-xl-h5 font-italic font-weight-light mt-3"
                            text
                            @click="toHome"
                        >
                            ...{{ $t("navigation.skip") }}
                        </v-btn>
                        <v-alert
                            border="top"
                            colored-border
                            class="text-subtitle-2 text-sm-subtitle-2 text-md-h6 text-lg-h5 text-xl-h5 font-weight-light"
                            type="info"
                            elevation="2"
                            color="secondary"
                        >
                            {{ $t("login_page.hint") }}
                        </v-alert>
                    </v-row>
                </v-form>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import firebase from "firebase";
import EventBus from "@/eventbus";
import i18n from "@/i18n";
export default {
    data: () => ({
        valid: false,
        show: false,
        nickname: "",
        isLogin: false,
        label: "login",
        email: "",
    }),

    methods: {
        toHome() {
            this.$router.push("/");
            this.$store.dispatch("setTitle", "empty");
        },

        toSignup() {
            this.$router.push("/signup");
            this.$store.dispatch("setTitle", "sign up");
        },

        logInOut() {
            if (this.isLogin) {
                firebase
                    .auth()
                    .signOut()
                    .then(() => {
                        this.isLogin = false;
                        this.label = "Login";
                        this.$store.dispatch("setAccessLevel", 0);
                        this.$store.dispatch("setUser", "");
                    })
                    .catch((error) => {
                        console.log("could not signed out" + error);
                    });
            } else if (this.$refs.form.validate()) {
                firebase
                    .auth()
                    .signInWithEmailAndPassword(this.email, this.nickname)
                    .then(() => {
                        this.isLogin = true;
                        this.label = "Logout";
                        this.$store.dispatch("getAccessLevel", this.email);
                        this.$store.dispatch("setUser", {nickname: this.nickname, email: this.email});
                        this.toHome();
                    })
                    .catch(() =>
                        EventBus.showErrorMessage(
                            i18n.t("login_page.login failed")
                        )
                    );
            }
        },
    },

    computed: {
        isValidated() {
            return this.valid;
        },
    },
};
</script>
<style scoped></style>
