<template>
    <div>
        <v-menu offset-y v-model="menuValue">
            <template v-slot:activator="{ on }">
                <v-btn
                    text
                    pl-0 pr-2 ml-0
                    @click.stop="drawer = !drawer"
                    v-on="on"
                    :class="{ active: menuValue }"
                    ><v-icon xLarge>mdi-menu</v-icon></v-btn
                >
            </template>
        </v-menu>

        <v-navigation-drawer
            v-model="drawer"
            absolute
            temporary
            right
            app
            clipped
            color="secondary"
            height="16em"
        >
            <v-treeview :items="getRoutes" v-model="tree" expand-icon="mdi-chevron-down">
                
                <template  slot="label" slot-scope="props">
                    <v-icon v-if="props.item.icon">{{
                        props.item.icon
                    }}</v-icon>
                    <router-link
                        class="navigatorLink"
                        :to="props.item.route"
                        v-if="props.item.route"
                        @click.native="setTitle(props.item.title)"
                    >
                        {{ $t(props.item.name) }}
                    </router-link>
                </template>
            </v-treeview>
            <v-treeview v-if="getUser !== ''">
                <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <template v-slot:activator="{ on, attrs }">
          <span color="secondary"
          dark
          v-bind="attrs"
          class="settingsnavigator ml-9"
          v-on="on">
        <v-icon>mdi-tools
         
        </v-icon> Settings</span>
      </template>
      <v-card>
        <v-toolbar
          dark
          color="primary"
        >
          <v-btn
            icon
            dark
            @click="dialog = false"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>{{$t('settings.settings')}}</v-toolbar-title>
          <v-spacer></v-spacer>
          </v-toolbar>
  
        <v-divider></v-divider>
        <v-list
          three-line
          subheader
        >
          <v-subheader>{{$t('general')}}</v-subheader>
          <v-list-item>
            <v-list-item-action>
    <v-checkbox
      v-model="checkbox"
      @change="setNewsLetter">
      </v-checkbox>

            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>{{$t('settings.newsletter')}}</v-list-item-title>
              <v-list-item-subtitle>{{$t('settings.newsletter description')}}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
           </v-list>
      </v-card>
    </v-dialog>
            </v-treeview>
             <v-row justify="center">
    
  </v-row>
        </v-navigation-drawer>
    </div>
</template>

<script>
export default {
    data: () => ({
        menuValue: false,
        drawer: false,
        group: null,
        tree: [],
        items: [],
        dialog: false,
        notifications: false,
        sound: true,
        widgets: false,
        checkbox: false,
       
    }),

    watch: {
        group() {
            this.drawer = false;
        },
    },

    methods: {
        setTitle(title) {
            this.$store.dispatch("setTitle", title);
        },

        setNewsLetter(enabled){
            console.log(enabled)
        this.$store.dispatch("addNewsletterSubscription", {user: this.$store.getters.getUser, state: enabled});
            
        }
    },

    mounted() {
        //this.items = this.$store.getters.getRoutes
    },

     computed: {        
        getRoutes() {
        
            return this.$store.getters.getRoutes;
        },
        getUser() {
            return  this.$store.getters.getUser;
        }
    },
};
</script>

<style scoped>
.v-btn.active .v-icon {
    transform: rotate(-360deg);
}
.navigatorLink {
    margin-top: 0.1em;
    color: white;
    text-decoration: none;
}
</style>
