import Vue from "vue";
import Vuex from "vuex";
import { vuexfireMutations, firestoreAction } from 'vuexfire'
import { db } from './db'
import { filterRoutes, filterAboutContents } from './navigation'


Vue.use(Vuex);

import Cv_en from "@/i18n/content/cv_en.json";
import Skills from "@/i18n/content/skills.json";
import Hobbies_en from "@/i18n/content/hobbies_en.json";
import Values_en from "@/i18n/content/values_en.json";
import Travels_en from "@/i18n/content/travels_en.json";


const filterCareerSteps = (careerSteps, filters) => {
    let filteredCareerSteps = [];
            careerSteps.forEach((careerElement) => {
                filters.forEach((filter) => {
                    if (careerElement.type === filter) {
                        filteredCareerSteps.push(careerElement);
                    }
                });
            });
     return filteredCareerSteps;
  }

  const filterSkillLanguage = (skills, language) => {
    skills.forEach((cat) => {
        cat.translatedCategory = deserialize(cat.category)[language]
        cat.content.forEach((content) => { 
            content.translatedDescription = deserialize(content.description)[language];     
            if(content.usage != undefined){
                content.translatedUsage =  deserialize(content.usage)[language];     
            }                 
        })
    })
  }

const deserialize = (json) =>  JSON.parse(JSON.stringify(json));

const filterAccess = (accessLevel, items) => {
    return items.filter(item => item.access <= accessLevel);
}



const importCvContent = (language) => import("@/i18n/content/cv_"+language+".json");
const importHobbiesContent = (language) => import("@/i18n/content/hobbies_"+language+".json");
const importValuesContent = (language) => import("@/i18n/content/values_"+language+".json");

export default new Vuex.Store({
    state: {
        accessLevel: 0,
        careerSteps: Cv_en,
        hobbies: Hobbies_en,
        values: Values_en,
        travels: Travels_en,
        filteredCareerSteps: [],
        certificateIconUrl: "@/assets/certificate.svg",
        filters: [],
        skills: Skills,
        title: 'empty',
        guestbook: [],
        username: '',
        email: '',
        filteredRoutes: [],
        
    },
    mutations: {
        ...vuexfireMutations,

        FILTER_CAREER_STEPS(state, filters) {
            state.filters = filters;
   
            state.filteredCareerSteps = filterCareerSteps(state.careerSteps, filters);          
        },
        CHANGE_LANGUAGE(state, language) {
            importCvContent(language).then(function(data) {
                state.careerSteps = data.default;
                state.filteredCareerSteps = filterCareerSteps(state.careerSteps, state.filters); 
            });
            filterSkillLanguage(state.skills, language)
            importHobbiesContent(language).then(function(data) {
                state.hobbies = data.default;
            });
            importValuesContent(language).then(function(data) {
                state.values = data.default;
            });
        }, 
        SET_TITLE(state, newTitle) {
            state.title = newTitle;       
        },
        SET_USER(state, user) {
            state.username = user.nickname; 
            state.email = user.email 
        },
        SET_ACCESS(state, access) {
            state.accessLevel = access;     
            state.filteredRoutes = filterRoutes(state.accessLevel)
        },

    },
    actions: {
        setFilteredCareerSteps(state, filters) {
            state.commit("FILTER_CAREER_STEPS", filters);
        },
        setLanguage(state, language) {
            state.commit("CHANGE_LANGUAGE", language);                  
        },
        setTitle(state, newTitle){
            state.commit("SET_TITLE", newTitle);
        },
        setUser(state, user){
            state.commit("SET_USER", user);  
        },
        setAccessLevel(state, access){
            state.commit("SET_ACCESS", access);
         },

         bindGuestbook: firestoreAction(({ bindFirestoreRef }) => {
            return bindFirestoreRef('guestbook', db.collection('guestbook'))
          }),
          unbindGuestbook: firestoreAction(( { unbindFirestoreRef }) => {
            unbindFirestoreRef('guestbook')
          }),

          getAccessLevel: firestoreAction(({ commit }, user) => {
          return db.collection('accessLevel').where('email', "==", user).get()
          
          .then(function(querySnapshot) {
            querySnapshot.forEach(doc => {
                commit("SET_ACCESS", doc.data().access);
                                });
        })
        .catch(function(error) {
            console.log("Error getting documents: ", error);
        }) 
         
         
          }),
     
          addGuestbookEntry: firestoreAction((context, guestbookentry) => {
            return db.collection('guestbook').add(guestbookentry)
          }),

          addNewsletterSubscription: firestoreAction((context, data) => {
                    return db.collection('accessLevel').where('email', "==", data.user).get()
               .then(function(querySnapshot) {
                querySnapshot.forEach(doc => {
                         db.collection('accessLevel').doc(doc.id).update({
                        newsletter: data.state})
                });
            })
            .catch(function(error) {
                console.log("Error: ", error);
            });


          }),
          addNewUser: firestoreAction((context, email) => {
            return db.collection('accessLevel').add({email, access: 1, newsletter: false})
       })
       

    },
    modules: {},
    getters: {
        getFilteredCareerSteps: (state) => state.filteredCareerSteps,
        getCertificateIconUrl: (state) => state.certificateIconUrl,

        getSkills: (state) => state.skills,
        
        getHobbies: (state) => deserialize(filterAccess(state.accessLevel, state.hobbies)),
        getValues: (state) => deserialize(state.values),
        getTravels: (state) => deserialize(filterAccess(state.accessLevel, state.travels)),
        
        getTitle: (state) => state.title,
        getGuestBookEntries: (state) => state.guestbook,
        
        getAccessLevel: (state) => state.accessLevel,
        getRoutes: (state) => state.filteredRoutes,
        getUsername: (state) => state.username,
        getAboutContents: (state) => filterAboutContents(state.accessLevel),
    },
});
