<template>
    <div :style="getImage" class="home">
        <v-container class="viewcontainer" fluid>
  <v-row justify="center" align="center">
        <v-col cols="12" class="pa-0">
             <WelcomeCard v-bind:color="'primary'" />
        </v-col>
    </v-row>
        </v-container>
    </div>
</template>

<script>
import WelcomeCard from "@/components/home/WelcomeCard";
const getOrientedImage = (landscapeImage, portraitImage) => {
    return window.screen.orientation.type === "portrait-primary"
        ? portraitImage
        : landscapeImage;
};

export default {
    name: "Home",
    components: {
        WelcomeCard,
    },

    data() {
        return {
            imageLandscape: {backgroundImage: `url(${require("@/assets/homeLandscape.jpg")})`},
            imagePortrait: {backgroundImage: `url(${require("@/assets/homePortrait.jpg")})`},
            image: {},
        };
    },

    methods: {
        handleOrientationChange() {
            this.image = getOrientedImage(this.imageLandscape, this.imagePortrait);
        },
    },

    mounted() {
        window.addEventListener(
            "orientationchange",
            this.handleOrientationChange
        );
        this.image = getOrientedImage(this.imageLandscape, this.imagePortrait);
    },

    computed: {
        getImage() {
            return this.image;
        },
    },
};
</script>
<style scoped>
.home {
  margin-top: 0;
}
</style>
