<template>
    <div>
        <v-app-bar
            class="appbar"
            fixed
            color="primary"
            dark
            shrink-on-scroll
            src="../assets/appbar.svg"
            fade-img-on-scroll
            dense
        >
            <v-app-bar-title class="appbartitle">
                <div class="appbartitle display-1 font-italic apptitle">
                    {{ $t(`navigation.${getTitle.toLowerCase()}`) }}
                </div>
            </v-app-bar-title>
            <v-layout>
                <v-spacer />             
                <v-btn text @click="languageChanged"
                    class="text-h6 text-sm-h4 text-md-h4 text-lg-h4 text-xl-h4 font-weight-light mt-1"> 
                    <CustomIcon v-if="getLanguage ==='de'" v-bind:icon="'germanIcon.svg'" v-bind:width="24" v-bind:height="24"/>
                    <CustomIcon v-if="getLanguage ==='en'" v-bind:icon="'englishIcon.svg'" v-bind:width="24" v-bind:height="24"/>
                </v-btn>
                <v-btn v-if="getUsername !== ''" @click="logout" icon>
                    <v-icon large>mdi-logout</v-icon>
                </v-btn>
                <v-btn v-if="getUsername === ''" @click="toLogin" icon>
                    <v-icon large>mdi-login</v-icon>
                </v-btn
                ><NavigationDrawer class="navigator mt-1" />
            </v-layout>
        </v-app-bar>
    </div>
</template>

<script>
import NavigationDrawer from "./NavigationDrawer";
import CustomIcon from "./utils/CustomIcon";
import firebase from "firebase";

export default {
    name: "ToolBar",

    components: {
        NavigationDrawer,
        CustomIcon
    },
    data() {
        return {
            language: String,
        };
    },

    methods: {
        logout() {
            firebase
                .auth()
                .signOut()
                .then(() => {
                    this.isLogin = false;
                    this.label = "Login";
                    this.$store.dispatch("setAccessLevel", 0);
                    this.$store.dispatch("setUser", {nickname: '', email: ''});
                     })
                .catch((error) => {
                    console.log("could not signed out" + error);
                });
        },

        toLogin() {
            this.$router.push("/login"); 
            this.$store.dispatch("setTitle", "login");
        },

        languageChanged: function() {
            this.$i18n.locale = this.$i18n.locale === "en" ? "de" : "en";
            this.$store.dispatch("setLanguage", this.$i18n.locale);
        },
    },

    computed: {
        getTitle() {
            return this.$store.getters.getTitle;
        },

        getUsername() {
            return this.$store.getters.getUsername;
        },

        getLanguage() {
            return this.$i18n.locale == "en" ? "de" : "en";
        },
    },
};
</script>

<style scoped>
.apptitle {
    min-width: 250px;
}

.navigator {
    margin-right: 0.5em;
}
</style>
