<template lang="">
    <div>
        <v-card :to="route" exact tile elevation="3">
            <v-img
                :src="require(`@/assets/${cardImage}`)"
                class="white--text align-end"
                gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                :height="getHeight"
                @click.native="setTitle()"
            >
                <v-card-title
                    class="display-1"
                    v-text="$t(cardTitle)"
                ></v-card-title>
            </v-img>
        </v-card>
    </div>
</template>
<script>
export default {
    name: "AboutNavigatorCard",

    props: {
        cardImage: String,
        cardTitle: String,
        route: String,
    },

    data() {
        return {
            orientation: "portrait-primary",
        };
    },

    computed: {
        getHeight() {
            console.log(
                "height is: " +
                    this.height +
                    this.orientation +
                    this.$vuetify.breakpoint.name
            );
            if (this.$vuetify.breakpoint.name === "xl") {
                return "500";
            }
            if (this.orientation === "portrait-primary") {
                switch (this.$vuetify.breakpoint.name) {
                    case "xs":
                        return "100";
                    case "sm":
                        return "200";
                    case "lg":
                        return "400";
                    default:
                        return "200";
                }
            } else {
                switch (this.$vuetify.breakpoint.name) {
                    case "xs":
                        return "100";
                    case "sm":
                        return "100";
                    case "md":
                        return "300";
                    case "lg":
                        return "500";
                    default:
                        return "200";
                }
            }
        },
    },

    mounted() {
        window.addEventListener(
            "orientationchange",
            this.handleOrientationChange
        );
    },

    methods: {
        handleOrientationChange() {
            this.orientation = window.screen.orientation.type;
        },
        setTitle() {
            this.$store.dispatch(
                "setTitle",
                this.cardTitle
                    .toString()
                    .split(".")
                    .pop()
            );
        },
    },
};
</script>
<style lang=""></style>
