<template lang="">
    <div>
        <CareerStepFilter />
        <div ref="content">
            <v-timeline align-top class="d-none d-sm-block" :dense="$vuetify.breakpoint.lgAndDown">
                <v-timeline-item
                    v-for="(careerStep, i) in filteredSteps"
                    :key="i"
                    :color="careerStep.color"
                    :icon="careerStep.icon">
                    <CareerCard class="careercard" v-bind:careerStep="careerStep" />
                </v-timeline-item>
            </v-timeline>
            <div class="d-sm-none"
                v-for="(careerStep, i) in filteredSteps"
                    :key="i">
                    <CareerCard class="careercard" v-bind:careerStep="careerStep" /></div>
            </div>
    </div>
</template>
<script>
import CareerCard from "./cv/CareerCard";
import CareerStepFilter from "./cv/CareerStepFilter";

import jsPDF from "jspdf";
import domtoimage from "dom-to-image";

import EventBus from "@/eventbus";
import i18n from "@/i18n";

export default {
    name: "TimeLine",

    components: {
        CareerCard,
        CareerStepFilter,
    },

    methods: {
        downloadPdf() {
            EventBus.showInfoMessage(i18n.t("messages.pdf_download_info"));
            let el = this.$refs.content;
            let scale = 0.6;
            domtoimage
                .toPng(el, {
                    width: el.clientWidth,
                    height: el.clientHeight,
                    quality: 1,
                    style: {
                        transform: "scale(" + scale + ")",
                        "transform-origin": "top left",
                    },
                })
                .then(function(dataUrl) {
                    var img = new Image();
                    img.src = dataUrl;
                    const doc = new jsPDF({
                        orientation: "portrait",
                        unit: "mm",
                        format: [800, 297],
                    });
                    doc.addImage(img, "JPEG", 20, 20);
                    const date = new Date();
                    const filename =
                        "CurriculumVitae" +
                        date.getFullYear() +
                        ("0" + (date.getMonth() + 1)).slice(-2) +
                        ("0" + date.getDate()).slice(-2) +
                        ("0" + date.getHours()).slice(-2) +
                        ("0" + date.getMinutes()).slice(-2) +
                        ("0" + date.getSeconds()).slice(-2) +
                        ".pdf";
                    doc.save(filename);
                })
                .catch(function(error) {
                    console.error("oops, something went wrong!", error);
                });
        },
    },

    computed: {
        filteredSteps() {
            return this.$store.getters.getFilteredCareerSteps;
        },
    },
};
</script>
<style scoped>
.careercard {

    margin-top: 1em;
}
</style>
