<template lang="">
    <div class="travel">
      
         <TravelCard
                v-for="(travel, i) in getTravels"
                :key="i"
                :title="travel.title"
                :imageName="travel.image"
                :chapters="travel.chapters"              
            />
    </div>
</template>

<script>

import TravelCard from "@/components/utils/CustomCard";

export default {
    name: "DialogTravels",

    components: {
        TravelCard
    },

    props: {
        btncolor: String,
    },

    data() {
        return {
            dialog: false,
            notifications: false,
            sound: true,
            widgets: false,
        };
    },
        computed: {
        getTravels() {
            return this.$store.getters.getTravels;
        },
    },
};
</script>
