<template lang="">
    <div>
        <v-container grid-list-md align="center">
            <v-layout row>
                <v-flex
                    xs12
                    md6
                    sm12
                    lg3
                    xl3
                    v-for="(entry, i) in guestbookentries"
                    :key="i"
                >
                    <v-hover>
                        <v-card class="">
                            <v-card-title>{{ entry.name }}</v-card-title>
                            <v-card-text>
                                <v-row class="pl-1">
                                    <v-rating
                                        :value="entry.rating"
                                        background-color="highlight lighten-3"
                                        color="highlight"
                                        dense
                                        half-increments
                                        readonly
                                        size="20"
                                    ></v-rating>
                                </v-row>

                                <div class="pt-5">{{ entry.message }}</div>
                                <div>{{ getDateString(entry.date) }}</div>
                            </v-card-text>
                        </v-card>
                    </v-hover>
                </v-flex>
            </v-layout>
        </v-container>
    </div>
</template>
<script>
export default {
    methods: {
        getDateString(date) {
            return new Date(date.seconds * 1000).toDateString();
        },
    },

    mounted() {
        this.$store.dispatch("bindGuestbook");
    },

    computed: {
        guestbookentries() {
            return JSON.parse(
                JSON.stringify(this.$store.getters.getGuestBookEntries)
            );
        },
    },
};
</script>
<style lang=""></style>
