<template lang="">
    <div>
        <div class="tasktitle" v-for="(task, i) in tasks" :key="i" fill-dot>
            <DescriptionList
                v-bind:descriptions="task.description"
                v-bind:title="task.title"/>
            <v-card-actions>
                <LinkButton
                    v-if="task.link"
                    v-bind:link="task.link"
                    v-bind:description="$t('Try it out')"/>
                <DownLoadButton
                    v-if="task.attachement"
                    v-bind:attachement="task.attachement"
                    v-bind:type="task.attachementType"
                    v-bind:description="task.actionName"/>
            </v-card-actions>
        </div>
    </div>
</template>

<script>
import DescriptionList from "@/components/utils/DescriptionList";
import LinkButton from "@/components/utils/LinkButton";
import DownLoadButton from "@/components/utils/DownLoadButton";
export default {
    name: "CareerStepTask",

    components: {
        DescriptionList,
        LinkButton,
        DownLoadButton,
    },

    props: {
        tasks: Array[Object],
    },
};
</script>
