<template lang="">
    <div>
        <v-container >
            <v-row  >
                <v-col cols="6" dense >
                    <SkillComponent class="skillcomponent"
                        v-for="(skill, i) in firstrow(skills)"
                        :key="i"
                        
                        :skill="skill"/>
                </v-col>
                <v-col cols="6" >
                    <SkillComponent class="skillcomponent"
                        v-for="(skill, i) in secondrow(skills)"
                        :key="i"
                        :skill="skill"/>
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<script>
import SkillComponent from "./SkillComponent";

export default {
    name: "TwoColumnSkillList",

    components: {
        SkillComponent,
    },

    props: {
        skills: Array,
    },

    methods: {
        firstrow(array) {
            if(array.length%2 == 0){
                return array.slice(0, (array.length/2));
            }
            return array.slice(0, (array.length / 2)+1);
        },
        secondrow(array) {
            if(array.length%2 == 0){
                return array.slice((array.length / 2), array.length);
            }
            return array.slice((array.length / 2)+1, array.length);
        },
    },
};
</script>
<style scoped>
.skillcomponent {
margin-top: 1em;
}
</style>
