<template lang="">
<div class="cvfiltermenu">
    <v-menu bottom :close-on-click="true" :close-on-content-click="false"> 
        <template v-slot:activator="{ on, attrs }">
            <v-btn text large v-bind="attrs" v-on="on">
                <v-icon large left>mdi-filter-menu</v-icon>
                <h3>{{$t('btn.filter')}}</h3>
            </v-btn>
        </template>

        <v-list>
            <v-list-item v-for="(filter, i) in filters" :key="i">
                <v-checkbox
                    v-model="checkedFilters"
                    @change="filterChanged($event)"
                    
                    :label='$t(`${filter.title}`)'
                    :value="filter.value"
                ></v-checkbox>
            </v-list-item>
        </v-list>
    </v-menu>

    </div>
</template>
<script>

export default {
    name: "CareerStepFilter",

    data() {
        return {
            checkedFilters: [ "certification", "education", "employment" ],
            filters: [
                { title: "show_certification", value: "certification" },
                { title: "show_education", value: "education" },
                { title: "show_employment", value: "employment" },
                { title: "show_military", value: "military" }, 
            ], 
                
        };
    },

    mounted: function() {
        this.filterChanged();
    },

    methods: {
        filterChanged: function() {
            this.$store.dispatch("setFilteredCareerSteps", this.checkedFilters);
        }, 
    },
};

</script>
