<template lang="">
    <v-hover v-slot="{ hover }" open-delay="50">
        <v-card
            class="mx-auto my-12"
            max-width="374"
            :elevation="hover ? 16 : 2"
            :class="{ 'on-hover': hover }"
        >
            <CustomIcon v-bind:height="480" v-bind:icon="imageName" />
            <v-card-title color="secondary">
                {{ title }}
            </v-card-title>
            <v-row align="center" class="mx-0"> </v-row>
            <div v-for="(chapter, i) in chapters" :key="i">
                <v-card-subtitle>
                    {{ chapter.chaptertitle }}
                </v-card-subtitle>
                <v-row align="center" class="mx-0">  </v-row>
  
                <v-card-text  v-if='fixheight' class="fixedHeight" scrollable> {{ chapter.chaptercontent }} </v-card-text>
                <v-card-text v-else scrollable> {{ chapter.chaptercontent }} </v-card-text>
                <v-row align="center" class="mx-0">  </v-row>
                <v-col class="ma-0 pa-0">
                    <v-card-actions>
                <LinkButton v-if="chapter.link"
                        v-bind:link="chapter.link"
                        v-bind:description="chapter.description"/>

                <video v-if="chapter.video" width="100%" controls>
                    <source :src="require(`@/assets/${chapter.video}`)" type="video/mp4">
                    {{$t('browsererror')}}
                </video>
                <DownLoadButton v-if="chapter.attachement"
                        v-bind:attachement="chapter.attachement"
                        v-bind:type="chapter.attachementType"
                        v-bind:description="chapter.description"/>
                        </v-card-actions></v-col>
            </div>
            
        </v-card>
    </v-hover>
</template>
<script>
import CustomIcon from "./CustomIcon";
import DownLoadButton from "@/components/utils/DownLoadButton";
import LinkButton from "@/components/utils/LinkButton";
export default {
    name: "HobbyCard",

    components: {
        CustomIcon,
        DownLoadButton,
        LinkButton
    },

    props: {
        title: String,
        chapters: Array,
        imageName: String,
        fixheight: Boolean
    },
};
</script>
<style scoped>
   .fixedHeight{      
       min-height: 150px;
       max-height: 300px;
       overflow-y:auto
   } 
</style>

<style lang="sass" scoped>
.v-card.on-hover.theme--dark
  background-color: rgba(#FFF, 0.1)
  >.v-card__text
    color: #000


</style>

<i18n>
{
  "en": {
    "browsererror": "Your browser does not support HTML video."
  },
  "de": {
    "browsererror": "Dein Browser unterstützt HTML Video nicht."
  }
}
</i18n>