<template lang="">
    <v-hover v-slot="{ hover }" open-delay="50">
        <v-card
            light
            class="mr-1 cardheader"
            :elevation="hover ? 16 : 2"
            :class="{ 'on-hover': hover }"
        >
            <CustomIcon
                class="companyicon"
                v-bind:height="150"
                v-bind:icon="careerStep.companyimage"
            />
            <v-card-title class="white text--primary">
                {{ careerStep.description }}{{ careerStep.description2 }}
            </v-card-title>
            <v-card-subtitle class="white text--primary">
                <a v-bind:href="careerStep.companypage" class="companylink">
                    <span>{{ careerStep.company }}</span>
                </a>
                <br /><span>{{ careerStep.date }}</span>
                <span v-if="careerStep.pensum">({{ careerStep.pensum }})</span>
            </v-card-subtitle>
            <v-system-bar height="2" :color="careerStep.type"></v-system-bar>
            <v-card-text class="white text--primary">
                <CareerStepTask v-bind:tasks="careerStep.tasks" />
            </v-card-text>
        </v-card>
    </v-hover>
</template>
<script>
import CareerStepTask from "./utils/CareerStepTask";

import CustomIcon from "@/components/utils/CustomIcon";
export default {
    name: "CertificationStep",

    components: {
        CareerStepTask,
        CustomIcon,
    },

    props: {
        careerStep: Object,
    },
};
</script>
<style scoped>
.companyicon {
    padding-left: 1em;
    padding-top: 0.5em;
}


.companylink {
    color: blue;
    text-decoration: none;
}
</style>
